import { Component, OnInit } from '@angular/core';
import { AbstractControl, FormGroup } from '@angular/forms';
import { DeprecationsService } from '@backbase/foundation-ang/future';
import {
  PaymentFormFieldOptions,
  PaymentFormFieldConfig,
} from '@backbase/payment-orders-ang';
import { registerFormControl } from '../../helpers/form-helpers';

/**
 * @deprecated Will be removed in widget collection v3.11.0
 */
@Component({
  selector: 'bb-payord-credit-debit-selector',
  templateUrl: './payord-credit-debit-selector.html',
})
export class PayordCreditDebitSelectorComponent implements OnInit {
  options!: PaymentFormFieldOptions;
  config!: PaymentFormFieldConfig;
  group!: FormGroup;
  control!: AbstractControl;

  constructor(readonly deprecationsService: DeprecationsService) {
    /**
     * @TODO Remove this component in 3.11.0
     */
    deprecationsService.logDeprecatedFeature(`
      bb-payord-credit-debit-selector has been deprecated since 3.10.0, it will be removed in 3.11.0\n
      To define credit/debit options use \`creditDebitOptions\` property from \`ManualBatchTypeConfig\`
      type defined in \`@backbase/business-ang/batch\`
    `);
  }

  ngOnInit() {
    this.control = registerFormControl(
      this.group,
      this.config?.name,
      this.options?.validators,
      this.options?.asyncValidators,
      this.options?.defaultValue ||
        (this.options?.choices && this.options.choices[0]),
      this.config
    );
  }
}
